<template>
    <div class="popuBox">     
        <div style="height: 100%; width: 90%; padding: 45px 15px">
            <el-collapse  v-model="activeNames" @change="handleChange">
                <el-collapse-item :key="item.guid" v-for="item in forms" :title="item.FormTemplate.title" :name="item.FormTemplate.name">
                    <VueForm ref="form" v-model="item.data"  :schema="item.FormTemplate.template" @on-cancel="onCancel"  :formProps="{labelPosition:'left'}" :formFooter="{okBtn:'',cancelBtn:''}">
                    </VueForm>
                </el-collapse-item>
            </el-collapse>
        </div>
    <div>
        <el-button type="primary" @click="onSubmit"></el-button>
    </div>
    </div>
</template>

<style scoped>
.popuBox  {
  color: rgb(0, 150, 255);
}
</style>


<style >

.el-form-item__content{
    margin-left: 40%;
}
.formFooter_item{
    display: none;
}

</style>

<script>
import VueForm from '@lljj/vue-json-schema-form';
import {getForms,getformstemplatebygroup,GUID} from "../api/forms";
import {savelocalforms,submitForm,removelocalforms} from "../api/forms"
import { Notify } from 'vant';
//vuejsonschemaformsjsonschema,UIelementuiUI
//UIschema
export default {
    components:{VueForm},
    inject: ["eventHub"],
    data() {
        return {
            //
            ispushed:false,
            showforms: true,
            activeNames:"",
            //
            defaultValues:{},
            forms:[
                {
                    name:"form1",
                    guid:"baca1",
                    data: {},
                    FormTemplate: {
                        type: 'object',
                        required: [
                            'userName',
                            'age',
                        ],
                        properties: {
                            userName: {
                                type: 'string',
                                title: '',
                                default: 'Liu.Jun',
                            },
                            age: {
                                type: 'number',
                                title: ''
                            },
                            bio: {
                                type: 'string',
                                title: '',
                                minLength: 10,
                                default: '',
                                'ui:options': {
                                    placeholder: '',
                                    type: 'textarea',
                                    rows: 1
                                }
                            },
                            imgUrlList: {
                                title: "",
                                type: "array",
                                "ui:widget": "UploadWidget",
                                //action
                                "ui:action": "https://run.mocky.io/v3/78638190-1adb-4d32-8f6e-400068ffe286",
                                "default": [
                                ],
                                "items": {
                                    "type": "string"
                                }
                            }
                        }
                    },
                    uiSchema: {},
                }
            ],
        }
    },
    mounted(){
        //
        window.onSubmit=this.onSubmit.bind(this);
        this.eventHub.$on("clickformedit", this.onclick);
        //routergroup
        let group = this.$route.params.group;
        let that=this;
        if (group) {
            getForms(1, 10, group).then(dt => {
                
                if (dt.isok) {
                    if (dt.data && dt.data.length) {
                        this.forms = dt.data;
                        //
                        if (this.forms && this.forms.length > 0) {
                            that.activeNames = this.forms[0].FormTemplate.name;
                        }
                    } else {
                        throw "no data"
                    }

                } else {
                    throw "no data"
                }
            })
            .catch(err=>{
                
                that.getTemplate();
            })
            window.formguid=group;
            return;
        }
       
    },
    methods:{
        //
        getTemplate() {
            let that=this;
            //
            let tgroup = this.$route.query.group;
            if (tgroup) {
                getformstemplatebygroup(tgroup).then(dt => {
                    if (dt.isok) {
                        //groupguid
                        if (!window.formguid) {
                            let groupguid = GUID();
                            window.formguid = groupguid;
                        }
                        this.forms = dt.data.map(i => {
                            return {
                                FormTemplate: i,
                                data: window.defaultValues ? window.defaultValues : {},
                                name: GUID(),
                                group: window.formguid,
                                FormTemplateId: i.id,
                            }
                        });
                        if (this.forms && this.forms.length > 0) {
                            that.activeNames = this.forms[0].FormTemplate.name;
                        }
                    }
                })
            }
        },
        handleChange(){

        },
        onCancel(){
            this.eventHub.$emit("clickform",true);
        },
        onSubmit(){
            let isValidall = true;
            let allvalidate = this.$refs.form.forEach(i => {
                 i.$refs.genEditForm.validate((isValid, resData) => {
                    isValidall = isValidall && isValid
                });
            })
            if (!isValidall) {
                Notify({ type: 'primary', message: '' });
                return;
            }
            //
            submitForm(this.forms).then(dt=>{
                if(dt.isok)
                {
                    Notify({ type: 'primary', message: '' });
                    //
                    removelocalforms(this.formData).then(isok=>{
                        this.eventHub.$emit("clickform",true);
                    })
                }else
                {
                    Notify({ type: 'primary', message: '' });
                }
            })
        },
        //
        handlecloseicon(){
            this.eventHub.$emit("clickform",true);
        },
        //toolsEventhub,toolbar
        onclick(item){
            this.showforms = !this.showforms;
            if (item) {
                this.formData = item;
            }
        }
    },
    watch:{
         //localstorageapp,
        'formData': {
            handler: function (newval, oldval) {
                //localstorage
                //savelocalforms(newval);
            },
            deep:true
        }
    },
    beforeDestroy(){
        this.eventHub.$off("clickformedit",this.onclick);
    }
}
</script>
